import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { timeZones } from 'src/app/data/mock/common/timezone/data';

import { AppointmentProfileListService } from 'src/app/modules/admin/appointment/appointment-profile/appointment-profile-list//appointment-profile-list.service';

@Component({
  selector: 'appointment-create-profile',
  templateUrl: './appointment-create-profile.component.html',
  styleUrls: ['./appointment-create-profile.component.scss'],
  encapsulation  : ViewEncapsulation.None,
})
export class AppointmentCreateProfileComponent implements OnInit {

  isEdit:boolean = false;

  profile:any = {}
  timeZonesData:any = [];

  isUrlBox:boolean = true;
  isAvailabilityBox:boolean = false;
  isPersonalizeBox:boolean = false;

  availableHoursList:any = [];
  programRecurranceList:any = [];

  constructor(
    private _activatedRoute: ActivatedRoute,
    private _router: Router,
    private appointmentProfileListService: AppointmentProfileListService
  ) { }

  getSelectedZone(event) {
    this.profile.timeZone = event[0].id
  }

  getSelectedStartHour(event) {
    this.profile.startHourId = event[0].id
  }

  getSelectedEndHour(event) {
    this.profile.endHourId = event[0].id
  }

  selectRecurranceId(item) {
    if(item.checked){
      this.profile.recurranceId = item.recurranceId
    }
  }

  showAvailablityBox() {
    this.isUrlBox = false;
    this.isAvailabilityBox = true
  }

  showPersonalizeBox() {
    this.isAvailabilityBox = false;
    this.isPersonalizeBox = true
  }

  ngOnInit(): void {

    this.appointmentProfileListService.appointmentprofilelostdrawer.subscribe((res:any) => {
      if(res != null) {
        if(res.id != undefined && res.id){
          this.isEdit = true
          this.profile = res.data
        }
        else {
          this.isEdit = false
          this.profile = {}
        }
      }
    })

    this.availableHoursList = [
      {
        id: '1',
        value: '09:00 am'
      },
      {
        id: '2',
        value: '10:00 am'
      },
      {
        id: '3',
        value: '11:00 am'
      },
      {
        id: '4',
        value: '12:00 pm'
      },
      {
        id: '5',
        value: '01:00 pm'
      },
      {
        id: '6',
        value: '02:00 pm'
      },
      {
        id: '7',
        value: '03:00 pm'
      },
      {
        id: '8',
        value: '04:00 pm'
      }
    ]

    this.programRecurranceList = [
      {
        recurranceId: 1,
        value: 'Sunday'
      },
      {
        recurranceId: 2,
        value: 'Monday'
      },
      {
        recurranceId: 3,
        value: 'Tuesday'
      },
      {
        recurranceId: 4,
        value: 'Wednesday'
      },
      {
        recurranceId: 5,
        value: 'Thursday'
      },
      {
        recurranceId: 6,
        value: 'Friday'
      },
      {
        recurranceId: 7,
        value: 'Saturday'
      }
    ]

    this.timeZonesData = timeZones.map((data=>{
      data.customLabel = `${data.region} ${data.text}`
      return data;
    }));

  }

}
